$desktop: "only screen and (min-width: 1024px) and (orientation: landscape)";

@import "mainText";
@import "workGallery";
@import url("https://fonts.googleapis.com/css2?family=Fira+Code:wght@400;700&display=swap");

* {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  background: #494949;
  color: white;
  font-family: "Fira Code", monospace;

  .container {
    display: flex;
    flex-direction: column;
  }
}

@media #{$desktop} {
  body {
    .container {
      flex-direction: row;
    }
  }
}
